.catalog {
    padding-bottom: 100px;
    &__tabs {
        margin-top: 70px;
        list-style-type: none;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-bottom: 0;
        padding-left: 0; }
    &__tab {
        width: 220px;
        height: 50px;
        border: 2px solid #d0d0d0;
        color: #6b6b6b;
        font-size: 18px;
        font-weight: 500;
        text-transform: uppercase;
        text-align: center;
        line-height: 48px;
        transform: skewX(-25deg);
        margin: 0 5px;
        cursor: pointer;
        transition: 0.5s all;
        &:hover {
            box-shadow: 3px 3px 20px $main-color;
            border: none; }
        div {
            transform: skewX(25deg); }
        &_active {
            background-color: $main-color;
            color: #fff;
            border: none; } }
    &__content {
        margin-top: 69px;
        display: none;
        justify-content: space-between;
        flex-wrap: wrap;
        &_active {
            display: flex; } }
    &-item {
        width: 300px;
        min-height: 440px;
        border: 2px solid #e8e8e8;
        background-color: #ffffff;
        padding: 20px 20px 26px 20px;
        overflow: hidden;
        margin-bottom: 20px;
        &__img {
            max-width: 100%;
            height: 200px;
            display: block;
            margin: 0 auto; }
        &__subtitle {
            margin-top: 20px;
            text-align: center;
            color: #0d0d0d;
            font-size: 16px;
            font-weight: 700; }
        &__descr {
            margin-top: 16px;
            text-align: center;
            color: #0d0d0d;
            font-size: 14px;
            font-weight: 300; }
        &__link {
            display: block;
            margin-top: 17px;
            color: $main-color;
            font-size: 14px;
            font-weight: 500;
            text-decoration: underline;
            text-align: center;
            text-transform: uppercase;
            &:hover {
                color: $main-color; } }
        hr {
            margin-top: 19px;
            margin-bottom: 21px;
            border-bottom: none;
            background-color: #e8e8e8; }
        &__footer {
            display: flex;
            justify-content: space-between;
            align-items: center; }
        &__old-price {
            color: #555555;
            font-size: 14px;
            font-weight: 300;
            text-decoration: line-through; }
        &__price {
            color: #050505;
            font-size: 20px;
            font-weight: 700; }
        &__wrapper {
            display: flex;
            width: calc(200% + 40px); }
        &__content {
            width: 50%;
            margin-right: 0px;
            transform: translateX(-100%);
            transition: 0.5s all;
            &_active {
                transform: translateX(0%);
                margin-right: 20px; } }
        &__list {
            position: relative;
            width: 50%;
            margin-left: 20px;
            transform: translateX(0%);
            padding-right: 20px;
            transition: 0.5s all;
            padding-left: 19px;
            margin-bottom: 0;
            list-style-type: none;
            &_active {
                transform: translateX(-100%);
                margin-left: 0px; }
            li {
                position: relative;
                color: #555555;
                font-size: 14px;
                font-weight: 300;
                margin-bottom: 20px;
                &:before {
                    content: '';
                    position: absolute;
                    width: 5px;
                    height: 5px;
                    background-color: $main-color;
                    left: -14px;
                    top: 7px; } } }
        &__back {
            position: absolute;
            bottom: 0;
            left: 50%;
            transform: translateX(calc(-50% - 19px));
            display: block;
            text-align: center;
            color: $main-color;
            font-size: 14px;
            font-weight: 500;
            text-decoration: underline;
            text-transform: uppercase;
            &:hover {
                color: $main-color; } } } }
